import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-delete-box',
  templateUrl: './delete-box.component.html',
  styleUrls: ['./delete-box.component.scss']
})
export class DeleteBoxComponent implements OnInit {

  @Input() inputname: string;
  @Input() inputid: string | number;

  @Output() actionEvt = new EventEmitter<boolean>();

  inputValue = '';

  stateParent(etat: boolean): void{
    this.actionEvt.emit(etat);
  }

  constructor() { }

  ngOnInit(): void {
  }

}
