import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppComponent } from './app.component';
import { MenuComponent } from './menu/menu.component';
import { ProductListComponent } from './product-list/product-list.component';
import { ProductComponent } from './product/product.component';
import {SupplierListComponent} from './supplier-list/supplier-list.component';
import { SupplierComponent } from './supplier/supplier.component';
import {AnonymizeBankAccountPipe} from './anonymize-bank-account.pipe';
import { SelectedBoxDirective } from './selected-box.directive';
import {SupplierService} from './supplier.service';
import {HttpClientModule} from '@angular/common/http';
import { SupplierAddComponent } from './supplier-add/supplier-add.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { ProductAddComponent } from './product-add/product-add.component';
import { HomeComponent } from './home/home.component';
import {RouterModule, Routes} from '@angular/router';
import { ProductDetailComponent } from './product-detail/product-detail.component';
import { DeleteBoxComponent } from './delete-box/delete-box.component';

const routes: Routes = [
  {path: 'products/add', component: ProductAddComponent},
  {path: 'products/:id', component: ProductDetailComponent},
  {path: 'products', component: ProductListComponent},
  {path: 'home', component: HomeComponent},
  {path: '', redirectTo: 'home', pathMatch: 'full'},
];

@NgModule({
  declarations: [
    AppComponent,
    MenuComponent,
    ProductListComponent,
    ProductComponent,
    SupplierListComponent,
    SupplierComponent,
    AnonymizeBankAccountPipe,
    SelectedBoxDirective,
    SupplierAddComponent,
    ProductAddComponent,
    HomeComponent,
    ProductDetailComponent,
    DeleteBoxComponent,
  ],
    imports: [
        BrowserModule,
        HttpClientModule,
        ReactiveFormsModule,
        RouterModule.forRoot(routes),
        FormsModule
    ],
  providers: [SupplierService],
  bootstrap: [AppComponent]
})
export class AppModule { }
